export const sendMessage = function (type, params) {
  return new Promise((resolve, reject) => {
    chrome.runtime.sendMessage({
      type,
      ...params,
    }, (result) => {
      resolve(result)
    })
  })
}

export const loadQuery = function (host, queryName, queryBundle) {
  return sendMessage('load_query', { host, queryName, queryBundle })
};

export const searchCategory = function (searchParam) {
  return sendMessage('category_search', { searchParam })
};

export const loadFullConfig = function () {
  return sendMessage('get_config')
};

export const track = function (...args) {
  sendMessage('track', {arguments: args})
};

export const getPurchaseMeta = function () {
  return sendMessage('get_purchase_meta')
};

export const getHasPurchased = async function () {
  const { productCount } = await getPurchaseMeta()
  return productCount && productCount > 0
};
